body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #282c34;
  color: #ffffff
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.navbar{
  overflow: hidden;
  background: lightseagreen;
  color: #ffffff
}
.container{
  width: 80%;
  margin: auto
}
.logo{
  font-size: 25px;
  width: 30%;
  float: left;
  display: block;
  margin:10px 0;
  color: #ffffff
}
ul{
  width: 70%;
  float: left;
  padding: 0;
  text-align: right
}
ul a{
  text-decoration: none;
  color: #ffffff;
  margin: 0 15px;

}
.active{
  background: #f00
}

.sofitel{
  width: 200px;
  height: 150px;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  float: right;
  border: 1px solid black;
  border-radius: 30px / 40px;
  box-shadow: 3px 3px 1px #A87767;
}
article
{
    display : flex;
    flex-direction: row-reverse;
    justify-content : center;
}
 
/*taille des colonnes*/
.element
{
    width: 900px;
    height: 450px;
    text-align: justify;
    margin: 10px;
    padding: 10px;
}
 
/*taille de la page*/
section
{
width: 100%;
min-width: 30%;
}
aside
{
    float: left;
    border: 1px solid #ffffff;
    border-radius: 30px / 40px; 
    background-color: black;
    width: 80px;
    height: 1000px;
}
.name {
  text-decoration: none;
  color: #ffffff;
  margin: 0 15px;
  text-align: justify;

}
form input{
  padding: 5px;
  background: #ebebeb;
  border: transparent;
  border: 0.3px solid #ccc

}
h1{
  color: aqua
  }
  .icon{
    cursor: pointer;
    color: #f01532;
    font-size: 29px;
  }
 ul  li{
   text-align: left

  }
  .home{
    display: flex;
   


  }
